.sidenavNone {
    height: 100%;
    width: 0%;
    position: fixed;
    top: 0;
    right: 0;
    background-color: white;
    overflow-x: hidden;
    display: grid;
    padding-top: 60px;
    opacity: 0;
}

.hamburger {
    display: grid;
    float: right;
    justify-content: flex-end;
}

.logo {
    padding-left: 6%;
    font-size: 2.3rem;
    font-weight: 600;
    z-index: 2;
}

.menu {
    height: 17px;
    position: fixed;
    right: 0;
    z-index: 2;
    width: 25px; 
    padding-right: 3%; 
    display: grid;
    justify-items: flex-end;
    transition: all .2s ease-in-out;
}

.menuActive{
    height: 20px;
    position: fixed;
    right: 0;
    z-index: 2;
    width: 30px;
    display: grid;
    padding-right: 3%; 
    transition: all .2s ease-in-out;
}

.mainMain {
    background-color: hsla(0,0%,93.3%,.7);
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 1;
    top: 0;
    left: 0;
}

.menu .line {
    float: right;
    height: 2.5px;
    width: 100%;
    background-color: #000000;
    transition: all .2s ease-in-out;
    border-radius: 100px;
}

.menu .line:nth-child(1) {
    width: 70%;
}

.menuActive .line:nth-child(1) {
    -webkit-transform: rotate(45deg) translateY(-7px);
            transform: rotate(45deg) translateY(-7px);
    -webkit-transform-origin: left top;
            transform-origin: left top;
    height: 2.5px;
    width: 100%;
    background-color: #000000;
    border-radius: 100px;
    transition: all .2s ease-in-out;
}

.menuActive .line:nth-child(2) {
    -webkit-transform: rotate(-45deg) translateY(7px);
            transform: rotate(-45deg) translateY(7px);
    -webkit-transform-origin: left bottom;
            transform-origin: left bottom;
    height: 2.5px;
    width: 100%;
    background-color: #000000;
    border-radius: 100px;
    transition: all .2s ease-in-out;
}

.menuActive:hover .line {
    background-color: #ffffff;
}

.menu:hover .line {
    background-color: #ffffff;
}

.sidenav {
    height: 100%;
    width: 50%;
    position: fixed;
    z-index: 1;
    top: 0;
    right: 0;
    background-color: #ffffff;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
    display: grid;
    justify-content: flex-start;
    align-items: center;
    grid-template-columns: 100%;
    display: grid;
    place-items: center;
}


.marque {
    width: 100%;
    height: 3.5rem;
    overflow: hidden;
    margin-bottom: 5%;
    white-space:nowrap;
}


.marque p:hover {
    position: relative;
    -webkit-animation: animate 0.2s linear;
            animation: animate 0.2s linear;
    top: 0;
}


@-webkit-keyframes animate {
    from {
        top: 0rem
    } to {
        top: -3rem
    }
}


@keyframes animate {
    from {
        top: 0rem
    } to {
        top: -3rem
    }
}

.sidenav p {
    position: relative;
    top: 0;
}

.text {
    margin-bottom: 14%;
    color: #434448;
}

.sidenav a {
    text-decoration: none;
}

.sidenav .links {
    margin-bottom: 8%;
    text-decoration: none;
    font-size: 2.5rem;
    color: #000000;
    display: block;
    transition: 0.3s;
    font-weight: 600;
}
  
  
.sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
}


@media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
}
